























import Vue from "vue";

export default Vue.extend({
  data() {
    return {
      collapsed: true,
    };
  },
  props: {
    question: {
      type: String,

      required: true,
    },
    answer: {
      type: String,
      required: true,
    },
  },
  computed: {
    questionClass() {
      if (this.collapsed) {
        return "questionCollapsed questionCommon";
      } else {
        return "questionFull questionCommon";
      }
    },
    questionClassText() {
      if (this.collapsed) {
        return "questionCollapsedText questionCommonText";
      } else {
        return "questionFullText questionCommonText";
      }
    },
  },
});
